@use 'assets/styles/index' as *;

.header {
  justify-content: space-between;
  align-items: center;
  margin-inline: auto;
  padding-inline: 20px;
  max-width: 1024px;

  @include respond-to(sm-desktop) {
    margin-bottom: 2rem;
  }

  @include respond-to(lg-desktop) {
    max-width: 1360px;
  }
}
