@font-face {
  font-family: 'icomoon';
  src:  url('/assets/icons/fonts/icomoon.eot?n8budr');
  src:  url('/assets/icons/fonts/icomoon.eot?n8budr#iefix') format('embedded-opentype'),
    url('/assets/icons/fonts/icomoon.ttf?n8budr') format('truetype'),
    url('/assets/icons/fonts/icomoon.woff?n8budr') format('woff'),
    url('/assets/icons/fonts/icomoon.svg?n8budr#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\e900";
}
.icon-instagram:before {
  content: "\e901";
}
.icon-pinterest:before {
  content: "\e902";
}
.icon-twitter:before {
  content: "\e904";
}
.icon-youtube:before {
  content: "\e905";
}
